<template>
  <div class="draw">
    <canvas ref="canvas"></canvas>
    <div class="meta" v-if="!artwork.canvasData || !artwork.canvasData.length">
      NO DATA :-(
      <br>
      <br>
      id: {{artwork.id}}
      <br>
      title: {{artwork.title}}
      <br>
      size: {{artwork.size}}
      <br>
      created: {{artwork.created}}
      <br>
      updated: {{artwork.updated}}
      <br>
      brush title: {{artwork.brush.title}}
    </div>
    <grid v-if="loaded && isGridShow" :size="viewportSize"></grid>
    <grid-highlight v-if="loaded" :width="widthView" :size="viewportSize" :offset="cursorOffset"></grid-highlight>
    <cursor-point v-if="loaded" :offset="cursorOffset"></cursor-point>
    <touch
      @pinchzoomstart="pinchzoomstart"
      @pinchzoomchange="pinchzoomchange"
      @pinchzoomend="pinchzoomend"
      @movechange="movechange"
    ></touch>
  </div>
</template>

<script>
import Grid from "./grid";
import GridHighlight from "./grid-highlight";
import CursorPoint from "./cursor";
import Touch from "./touch";
import { mapState, mapMutations, mapGetters } from "vuex";
import { clearInterval } from "timers";
export default {
  data() {
    return {
      loaded: false,
      midpoint: null, // 
      viewportOffset: { x: 0, y: 0 }, // 
      cursorOffset: { x: 0, y: 0 } // 
    };
  },
  computed: {
    canvasView() {
      return this.$refs.canvas; // dom
    },
    ctxView() {
      return this.canvasView.getContext("2d"); //  canvas 2d
    },
    widthView() {
      return this.canvasView ? this.canvasView.clientWidth : -1; // 
    },
    canvasData() {
      return this.artwork.canvasData; // 
    },
    ctx() {
      return this.canvas.getContext("2d"); //  canvas 2d 
    },
    ...mapState(["canvas", "artwork", "isGridShow", "isPushing"]),
    ...mapGetters(["viewportSize"])
  },
  watch: {
    viewportOffset: {
      handler(val) {
        this.drawCanvasView(); // 
        this.setViewportOffset(val);
      },
      deep: true // , 
    },
    canvasData() {
      console.log(", ");
      this.drawCanvas();
    }
  },
  methods: {
    limit(num, min, max) {
      return Math.min(Math.max(min, num), max);
    },
    pinchzoomstart(e) {
      // 
      // console.log(e);
      this.midpoint = {
        x: Math.floor((e.midpoint.x / this.widthView) * this.artwork.size), // 
        y: Math.floor((e.midpoint.y / this.widthView) * this.artwork.size)
      };
    },
    pinchzoomchange(e) {
      // console.log(e);

      if (e.scale > 0.01 || e.scale < -0.01) {
        // ,
        if (e.scale > 0) {
          this.setViewportSize(this.viewportSize - 2); // -1
        } else if (e.scale < 0) {
          this.setViewportSize(this.viewportSize + 2); // +1
        }

        let max = this.artwork.size - this.viewportSize; // 

        this.viewportOffset = {
          // 
          x: this.limit(
            Math.floor(this.midpoint.x - this.viewportSize / 2),
            0,
            max
          ),
          y: this.limit(
            Math.floor(this.midpoint.y - this.viewportSize / 2),
            0,
            max
          )
        };
      }
    },
    pinchzoomend(e) {
      // console.log(e);
    },
    movechange(e) {
      // 
      let offset = {
          x: this.cursorOffset.x + e.x, // 
          y: this.cursorOffset.y + e.y
        },
        min = this.widthView * 0.12, // 0.12~0.88
        max = this.widthView * 0.88,
        viewMax = this.artwork.size - this.viewportSize; // 

      if (offset.x <= min)
        this.viewportOffset.x = this.limit(
          (this.viewportOffset.x -= 2),
          0,
          viewMax
        ); // 

      if (offset.x >= max)
        this.viewportOffset.x = this.limit(
          (this.viewportOffset.x += 2),
          0,
          viewMax
        );

      if (offset.y <= min)
        this.viewportOffset.y = this.limit(
          (this.viewportOffset.y -= 2),
          0,
          viewMax
        );

      if (offset.y >= max)
        this.viewportOffset.y = this.limit(
          (this.viewportOffset.y += 2),
          0,
          viewMax
        );

      if (
        this.viewportOffset.x <= 0 ||
        this.viewportOffset.x >= viewMax ||
        this.viewportOffset.y <= 0 ||
        this.viewportOffset.y >= viewMax
      ) {
        min = 0; // 
        max = this.widthView;
      }

      this.cursorOffset = {
        x: this.limit(offset.x, min, max), // 
        y: this.limit(offset.y, min, max)
      };
    },
    drawCanvas() {
      // , , 
      if (!this.canvasData || !this.widthView) return;

      this.ctx.clearRect(0, 0, this.artwork.size, this.artwork.size); // 

      let offset_x = 0, // 
        offset_y = 0;

      for (let x = 0; x < this.canvasData.length; x++) {
        for (let y = 0; y < this.canvasData[x].length; y++) {
          // 
          let color = this.canvasData[x][y] || '#ffffff'; // 

          this.ctx.fillStyle = color; // 
          this.ctx.fillRect(offset_x, offset_y, 1, 1); // 
          offset_x += 1; // 
          // console.log(offset_x, offset_y);
        }
        offset_x = 0; // 0
        offset_y += 1; // 
      }

      this.drawCanvasView(); // 

      // this.raf = requestAnimationFrame(() => {
      //   this.drawCanvas();
      // });
    },
    drawCanvasView() {
      // , , dom
      if (!this.canvasData || this.widthView === -1) return;

      this.canvasView.width = this.canvasView.height = this.widthView; // css

      this.ctxView.clearRect(0, 0, this.widthView, this.widthView); // 

      this.ctxView.mozImageSmoothingEnabled = false; // , 
      this.ctxView.webkitImageSmoothingEnabled = false;
      this.ctxView.msImageSmoothingEnabled = false;
      this.ctxView.imageSmoothingEnabled = false;

      this.ctxView.drawImage(
        // drawImage
        this.canvas,
        this.viewportOffset.x, // 
        this.viewportOffset.y,
        this.viewportSize, // 
        this.viewportSize,
        0,
        0,
        this.widthView,
        this.widthView
      );
    },
    init() {
      this.setViewportSize(this.artwork.size); // 
      this.drawCanvas(); // 
      this.timer = setInterval(() => {
        this.updateThumb(this.canvas.toDataURL()); // 1000ms
      }, 1000);
      this.cursorOffset = {
        x: this.widthView * 0.5, // 
        y: this.widthView * 0.5
      };

      this.loaded = true;
    },
    ...mapMutations([
      "setArtworkInfo",
      "updateThumb",
      "setViewportSize",
      "setViewportOffset"
    ])
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    // cancelAnimationFrame(this.raf);
    window.clearInterval(this.timer); // 
  },
  components: {
    Grid,
    GridHighlight,
    CursorPoint,
    Touch
  }
};
</script>

<style lang="scss" scoped>
.draw {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 368px;
  height: 368px;
  margin: 4px auto;
  border: 2px solid $black-color;
  canvas {
    width: 100%;
    height: 100%;
  }
  .meta {
    position: absolute;
    color: $black-color;
    font-size: 12px;
  }
}
</style>
