<template>
  <div class="brush-list" ref="brushList">
    <icon
      class="brush"
      name="brush"
      :style="{color: color}"
      :index="String(index)"
      :currentIndex="String(currentIndex)"
      v-for="(color, index) in artwork.brush.colors || Array(16).fill('#9b9b9b')"
      :key="index"
      @touchstart.native="selectStart"
      @touchmove.native="selectMove"
      @touchend.native="selectEnd"
      @click.native="selectStart"
      @mouseup.native="selectEnd"
    ></icon>
    <div class="mask"></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import _ from "lodash";
export default {
  data() {
    return {
      currentIndex: -1,
      width: -1
    };
  },
  computed: {
    ...mapState(["artwork"])
  },
  methods: {
    computedIndex(e) {
      // console.log(e);
      const target = e.touches || [e]; // 
      const clientX = target[0].clientX; // X
      if (clientX < 0 || clientX > this.width) return; // 

      const index = Math.floor((clientX / this.width / (10 / 16)) * 10); //  X   

      if (this.currentIndex !== index) this.currentIndex = index; // 
    },
    selectStart(e) {
      // console.log("start", e);
      this.width = this.$refs.brushList.clientWidth; // 
      this.computedIndex(e);
    },
    selectMove: _.throttle(function(e) {
      // lodash
      this.computedIndex(e);
    }, 50),
    selectEnd(e) {
      const color = this.artwork.brush.colors[this.currentIndex];
      console.log(
        ` %c${color}`,
        `background: ${color}; color: ${this.complement(color)}`
      );
      this.setArtworkInfo({ currentBrushColor: color });
    },
    complement(hex) {
      return;
      "#" +
        ("000000" + (0xffffff ^ parseInt(hex.slice(1), 16)).toString(16)).slice(
          -6
        );
    },
    ...mapMutations(["setArtworkInfo"])
  },
  mounted() {
    this.currentIndex = this.artwork.brush.colors.findIndex(item => {
      return item === this.artwork.currentBrushColor;
    });
  }
};
</script>

<style lang="scss" scoped>
.brush-list {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  width: 375px;
  .brush {
    width: 20px;
    height: 520px;
    transition: transform 0.2s;
    &.active {
      transform: translateY(-10px);
    }
  }
  .mask {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 120px;
    background: $white-color;
  }
}
@media screen and (min-width: 768px), screen and (max-height: 520px) {
  .brush-list .mask {
    height: 66px;
  }
}
</style>
